.menubtn {
    font-family: arial;
    color: #FFFFFF !important;
    font-size: 14px;
    text-shadow: 1px 1px 0px #C85ECC;
    box-shadow: 1px 1px 1px #BEE2F9;
    padding: 1px;
    -ms-transform: rotate(-10deg); /* IE 9 */
    transform: rotate(-10deg);
    -moz-border-radius: 0px 7px 0px 7px;
    -webkit-border-radius: 0px 7px 0px 7px;
    border-radius: 0px 7px 0px 7px;
    border: 2px solid #E26AEB;
    background: #F472FD;
}
.menubtnDrawer {
    font-family: arial;
    color: #FFFFFF !important;
    font-size: 14px;
    text-shadow: 1px 1px 0px #C85ECC;
    box-shadow: 1px 1px 1px #BEE2F9;
    padding: 1px;
    -ms-transform: rotate(-10deg); /* IE 9 */
    transform: rotate(-10deg);
    -moz-border-radius: 0px 7px 0px 7px;
    -webkit-border-radius: 0px 7px 0px 7px;
    border-radius: 0px 7px 0px 7px;
    border: 2px solid #E26AEB;
    background: #F472FD;
}

.menubtn:hover {
    color: #FFFFFF !important;
    background: #99f9a7f;
}
.menubtn:active{
    border:none;
}
.btn-install{
    background-color: #42eece;
    padding: 0.5rem 1rem;
    font-size: 1.25rem;
    border-radius: 0.3rem;
    display: inline-block;
    font-weight: bold;
    line-height: 1.5;
    color: #eb44fa;
    text-align: center;
    text-decoration: none;
    vertical-align: middle;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    border: 1px solid transparent;
    -webkit-box-shadow: 2px 9px 0px 3px rgba(30,205,172,1);
    -moz-box-shadow: 2px 9px 0px 3px rgba(30,205,172,1);
    box-shadow: 2px 9px 0px 3px rgba(30,205,172,1);
    padding: 0.375rem 0.75rem;
    font-size: 1.2rem;
    border-radius: 0.25rem;
    transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}
.btn-install:active, .btn-install:visited, .playBtn:active, .playBtn:visited{
    color: #FFF !important;
}

